import React from "react"
import styles from "./link-box.module.css"
import { Link } from "gatsby"

export default props => (
	<Link 
		to={props.to}
		className={styles.linkBox}
	>
		<div
			className={styles.innerBox}
			style={{ backgroundImage: `url(${props.boxImage})` }}
		>
			<p>{props.title}</p>
		</div>
	</Link>
)